@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,700;1,800&display=swap");
html,
body,
main,
section {
  overflow-x: hidden;
}

ul {
  padding: 0;
}

main {
  flex: 1 0 auto;
}

#hero {
  height: 787px;
  border-bottom-left-radius: 5em;
  background-image: url("../img/background-desk.png");
  background-color: white;
  background-size: cover;
  background-repeat: no-repeat;
  border-bottom-left-radius: 5rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#navbar > ul > li {
  margin-right: 2em;
  list-style: none;
}

.portfolio-items {
  width: 80%;
}

.about-section {
  width: 100%;
  border-top-right-radius: 5em;
  background-color: white;
}

ul {
  display: flex;
}

.project {
  margin-top: 40px;
  background-color: white;
  padding: 30px;
  border-radius: 15px;
}

.work-details {
  display: flex;
}

.ul-details {
  display: flex;
}

.ul-details > li {
  list-style: disc;
  margin-right: 2em;
}

.ul-tags {
  padding-left: 0;
}

.ul-tags li {
  padding: 3px 14px 3px 14px;
  background-color: #ebebff;
  border-radius: 8px;
  font-size: 12px;
  line-height: 16px;
  color: #6070ff;
  font-weight: 500;
  list-style: none;
  margin-right: 2em;
  margin-bottom: 3em;
}

body {
  background-color: white;
}

header {
  background-color: white;
}

main {
  background-color: #f7f7f9;
}

footer {
  color: white;
  background-color: #6070ff;
}

#contact {
  background-color: #6070ff;
  border-top-left-radius: 5em;
}

a,
p,
h1,
h2,
h3,
h4,
ul,
li,
form,
button {
  font-family: "Poppins", sans-serif;
}

.tools {
  background-color: #f7f7f9;
}